<template>
  <div class="login">
    <h1 class="title">Edu boss 管理系统</h1>
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      label-position="top"
      label-width="80px" >
      <h2 class="form-title">登录</h2>
      <el-form-item
        label="手机号"
        prop="phone" >
        <el-input
          v-model="form.phone" />
      </el-form-item>
      <el-form-item
        label="密码"
        prop="password" >
        <el-input
          v-model="form.password"
          show-password />
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          @click="onSubmit"
          :loading="isLogin">
            登录
          </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { login } from '@/services/user'

export default {
  name: 'LoginIndex',
  data () {
    return {
      form: {
        phone: '',
        password: ''
      },
      rules: {
        phone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { pattern: /^1\d{10}$/, message: '请输入正确手机号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 18, message: '密码长度6-18位', trigger: 'blur' }
        ]
      },
      isLogin: false
    }
  },
  methods: {
    async onSubmit () {
      try {
        await this.$refs.form.validate()
        this.isLogin = true
        const { data } = await login(this.form)
        this.isLogin = false
        if (data.state === 1) {
          this.$store.commit('setUser', data.content)
          this.$router.push(this.$route.query.redirect || { name: 'home' })
          this.$message.success('登录成功')
        } else {
          this.$message.error(data.message)
        }
      } catch (err) {
        console.log('没有通过校验', err)
      }
    }
  }
}
</script>

